* {
	margin: 0;
	padding: 0;
}

body,
html {
	height: 100%;
}

body {
	font-size: 14px;
	overflow-x: hidden;
	font-family: Montserrat;
	color: #000;
	background-color: #fafafa;
}

p {
	margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: Montserrat, 'Helvetica Neue', Helvetica, Roboto, Arial,
		sans-serif !important;
}

img {
	max-width: 100%;
}

iframe {
	width: 100% !important;
}

.p8 {
	padding: 8px;
}

.inline-list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

#content_editable,
.admin_edit {
	top: 100px !important;
}

.error404 {
	margin-top: 100px;
}

.banner {
	margin-bottom: 24px;
}

.breadcrumbs-box li {
	font-size: 12px;
}

.core_en {
	padding-top: 24px;
}

.off-canvas-wrap {
	min-height: calc(100% - 710px);
}

.up {
	position: fixed;
	bottom: 50px;
	right: 15px;
	width: 35px;
	cursor: pointer;
}

.share-btn li {
	margin-left: 0;
	margin-bottom: 0;
}

.wrapper {
	margin-bottom: 50px;
}

.tablet_buttons {
	display: none;
}

.tablet_buttons > div {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 15px;
}

.tablet_buttons a {
	width: 48%;
	height: 42px;
	border-radius: 2px;
	background-color: #fff;
	text-align: center;
	font-size: 16px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	padding-top: 10px;
}

.tablet_buttons .tablet_button1 {
	border: 1px solid #5287b0;
	color: #5287b0;
}

.tablet_buttons .tablet_button1:hover {
	border: 0;
	color: #fff;
	background: #5287b0;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}

.tablet_buttons .tablet_button2 {
	border: 1px solid #ef767a;
	color: #ef767a;
}

.tablet_buttons .tablet_button2:hover {
	border: 0;
	color: #fff;
	background: #ef767a;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}

button {
	outline: 0;
}

.border-bottom {
	border-bottom: 1px solid #5287b0;
	margin-bottom: 18px !important;
}

.hover_bg {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background: rgba(0, 0, 0, 0.1);
	z-index: 1;
	// display: none;
	visibility: hidden;
	transition: all 0.25s ease;
	opacity: 0;
	pointer-events: none;
	&.visible {
		opacity: 1;
		visibility: visible;
	}
}

.more {
	width: 100%;
	text-align: center;
	display: block;
	margin-top: 31px;
	-webkit-box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.06);
	box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.06);
	height: 36px;
	font-weight: 700;
	color: #1c2e36;
	padding-top: 10px;
	background-color: #fff;
}

.invest_btn:hover {
	background-color: #5287b0 !important;
	color: #fff !important;
}

.invest_btn1:hover {
	background-color: #ef767a !important;
	color: #fff !important;
}

.header_container {
	position: relative;
	margin-bottom: 40px;
	height: 81px;
}

.header {
	-webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
	background-color: #fff;
	position: fixed;
	padding: 0 15px;
	top: 0;
	width: 100%;
	z-index: 20;
}

.header > div > div {
	justify-content: space-between;
	align-items: center;
}

.header .inv_logo {
	padding: 19px 0;
}

.header .menu {
	font-weight: 700;
	// text-transform: uppercase;
	// padding-top: 5px;
	@media screen and (max-width: 1024px) {
		margin-left: -60px;
	}
}

.header .menu nav {
	display: flex;
}

.header .menu nav li {
	list-style-type: none;
	padding: 20px 0;
	position: relative;
}

.header .menu nav li a:hover {
	color: #5287b0;
}

.header .menu nav li a {
	color: #1c2e36;
}

.header .menu nav li .submenu {
	position: absolute;
	top: 75px;
	background: #fff;
	width: 480px;
	box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.1);
	display: none;
	z-index: 18;
}

.header .menu nav li .submenu a {
	padding: 8px 0;
	display: block;
}

.header .menu nav li .submenu .block1 {
	padding: 16px 24px 16px 24px;
}

