.header .menu nav li .submenu .block2 a:hover {
	color: #ef767a;
}

.header .menu nav li .submenu .block2 {
	background-color: #1c2e36;
	padding: 16px 24px 16px 24px;
}

.header .menu nav li .submenu .block2 a {
	color: #fff;
}

.header .menu_icons {
	display: none;
}

.header .menu_icons i {
	font-size: 24px;
}

.header .right {
	float: right;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	color: #5287b0;
	padding: 14px 0 10px 0;
}
.header .header__subscribe {
	font-weight: bold;
	padding-right: 5px;
	color: #5287b0;
}
.header .right div:nth-child(2) {
	padding: 0 5px;
}

.header .right div:last-child {
	position: relative;
	cursor: pointer;
}

.header .right div:first-child {
	padding-right: 10px;
	cursor: pointer;
}

.header .right .social a {
	color: #5287b0;
	font-family: Montserrat;
	font-weight: bold;
}

.header .right .social a:hover {
	color: #ef767a;
}

.header .right .my_adv {
	border-radius: 2px;
	background-color: #ef767a;
	color: #fff !important;
	font-weight: 600;
	padding: 4px 6px;
}

.header .right .lang {
	margin-left: 0;
}

.header .right .lang select {
	height: 27px;
	border: 0;
	margin: 0;
	padding: 0 15px 0 0;
	background: url(../i/chevron_down.png) no-repeat;
	background-position: right;
	color: #5287b0;
	font-weight: 700;
}

.header .right .seach {
	font-size: 19px;
	cursor: pointer;
}

.header_search {
	background-color: #1c2e36;
	margin-bottom: 40px;
	display: none;
	min-height: 64.5px;
	top: 0;
	position: fixed;
	width: 100%;
	z-index: 100;
}

.header_search .small-3 {
	border-right: 2px solid #fff;
}

.header_search .logo {
	padding: 19px 0;
	position: static;
}

.header_search .search_input {
	padding-top: 14px;
}

.header_search .search_input button {
	position: static;
	width: auto;
	height: auto;
	padding: 0;
	background-color: #1c2e36;
	cursor: pointer;
}

.header_search .search_input input {
	display: inline-block;
	max-width: 700px;
	border: 0;
	background-color: #1c2e36;
	color: #fff;
	margin-bottom: 0;
	box-shadow: none;
