	color: #fff;
	font-size: 18px;
}

.header_search .search_input #close {
	float: right;
	margin-top: 9px;
	cursor: pointer;
}

.mobile_menu {
	position: fixed;
	top: 64px;
	width: 291px;
	z-index: 20;
	display: none;
	background: #fff;
}

.mobile_menu .lang_switch {
	background: #fff !important;
	height: 64px;
	padding-top: 17.5px;
}

