#gm_callback {
	position: fixed;
	top: 20px;
	right: 16px;
	display: block;
	z-index: 150;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-start;
	color: #ffffff;
	display: none;
}
#gm_callback > .gm_call {
	width: 50px;
	height: 50px;
	border-radius: 25px;
	background: #4188b4;
	color: #ffffff;
	position: relative;
	padding: 0;
	box-sizing: border-box;
	cursor: pointer;
	text-align: center;
	overflow: hidden;
}
#gm_callback > .gm_call:before {
	display: block;
	content: '';
	font-family: FontAwesome;
	width: 100%;
	font-size: 32px;
	line-height: 50px;
	height: 100%;
}
#gm_callback > .gm_result {
	font-size: 18px;
	text-align: center;
	display: none;
	background: rgba(67, 136, 157, 0.8);
	border-radius: 6px;
	box-sizing: border-box;
	width: 300px;
	height: auto;
	padding: 12px;
	position: relative;
	line-height: 1.2;
	font-weight: bold;
	padding: 20px 30px;
}
#gm_callback .gm_close {
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	line-height: 1;
}
#gm_callback .gm_close:before {
	content: '';
}
#gm_callback > .gm_form {
	width: 0;
	height: 0;
	padding: 0;
	background: #4188b4;
	border-radius: 6px;
	box-sizing: border-box;
	overflow: hidden;
	position: relative;
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}
